.page-container{
    display: flex;
    grid-template-rows: 1fr;
    min-height: 100vh;
}

.email-list-search-column{
    display: flex;
    flex-direction: column;
    max-width: 500px;
    min-width: 500px;
    grid-column: 2/3;
    max-height: 100vh;
}

.email-list-container{
    overflow-y: scroll;
    scrollbar-width: none; 
    border-radius: 10px;
    padding-top: 0;
}

/* gets rid of scroll bar */
.email-list-container::-webkit-scrollbar{
    display: none;
}

.sidebar-column{
    grid-column: 1/2;
}

.body-column{
    grid-column: 3/4;
    width: 100%;
}
